import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';
import * as style from './index.module.scss';

const Footer = () => {
    return (
        <footer className={style.footer}>
            <nav>
                <div>
                    <a
                        href="mailto:contact@SNinjo.com"
                        target="_blank"
                        rel="noreferrer noopener"
                        title='Email'
                    >
                        <StaticImage
                            style={{width: 30}}
                            src='../../assets/icon/email.png'
                            alt='email logo'
                            loading='eager'
                        />
                    </a>
                    <a
                        href="https://www.linkedin.com/in/ru-jun-liao-3bba07196/"
                        target="_blank"
                        rel="noreferrer noopener"
                        title='Linkedin'
                    >
                        <StaticImage
                            style={{width: 23}}
                            src='../../assets/icon/linkedin.png'
                            alt='linkedin logo'
                            loading='eager'
                        />
                    </a>
                    <a
                        href="https://github.com/SNinjo"
                        target="_blank"
                        rel="noreferrer noopener"
                        title='Github'
                    >
                        <StaticImage
                            style={{width: 25}}
                            src='../../assets/icon/github_white.png'
                            alt='github logo'
                            loading='eager'
                        />
                    </a>
                    <a
                        href="https://www.instagram.com/sninjo657/"
                        target="_blank"
                        rel="noreferrer noopener"
                        title='Instagram'
                    >
                        <StaticImage
                            style={{width: 27}}
                            src='../../assets/icon/instagram.png'
                            alt='instagram logo'
                            loading='eager'
                        />
                    </a>
                    {/* eslint-disable-next-line react/jsx-no-target-blank */}
                    <a
                        id={style.blog}
                        href="https://blog.sninjo.com"
                        target="_blank"
                        title='Blog'
                    >
                        <StaticImage
                            style={{width: 25}}
                            src='../../assets/blog.png'
                            alt='SNinjo blog logo'
                            loading='eager'
                        />
                    </a>
                </div>
            </nav>
            <p>SNinjo 2022 © all rights reserved</p>
        </footer>
    )
}
export default Footer;
