import { PageProps } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import Icon from '../../components/Icon';
import Layout from '../../components/Layout';
import SEO from '../../components/SEO';

const ResumePage = () => {
    const RowIcons: FC<{names: Array<string>}> = ({ names }) => {
        return (
            <section>
                {names.map(iconName =>
                    <Icon
                        key={`icon_${iconName}`}
                        name={iconName}
                    />
                )}
            </section>
        )
    }

    return (
        <Layout>
            <div
                className={style.div}
            >
                <div className={style.aside}>
                    <section className={style.section}>
                        <h2>INFO</h2>
                        <hr/>
                        <ul>
                            <li>
                                <h4>Full name</h4>
                                <span>Ru-Jun Liao</span>
                                <span>廖儒均</span>
                            </li>
                            <li>
                                <h4>Nickname</h4>
                                <span>SNinjo, Jo</span>
                            </li>
                            <li>
                                <h4>Birthday</h4>
                                <span>Feb 25, 2001</span>
                            </li>
                            <li>
                                <h4>Spoken languages</h4>
                                <span>Chinese, English</span>
                            </li>
                        </ul>
                    </section>
                    <section className={style.section}>
                        <h2>SKILL</h2>
                        <hr/>
                        <ul>
                            <li>
                                <h4>WEB DEV</h4>
                                <RowIcons names={['HTML', 'CSS', 'SASS']} />
                                <RowIcons names={['JavaScript', 'TypeScript']} />
                                <RowIcons names={['Next.js', 'Gatsby.js', 'React.js']} />
                            </li>
                            <li>
                                <h4>SOFTWARE DEV</h4>
                                <RowIcons names={['Node.js', 'Express.js', 'Nest.js']} />
                                <RowIcons names={['Jest.js']} />
                                <RowIcons names={['Go', 'Testify']} />
                                <RowIcons names={['Python', 'FastAPI', 'Pytest']} />
                                <RowIcons names={['GraphQL', 'MongoDB', 'MySQL']} />
                                <RowIcons names={['Git', 'Github', 'Gitlab']} />
                            </li>
                            <li>
                                <h4>PLATFORM</h4>
                                <RowIcons names={['Docker']} />
                                <RowIcons names={['Linux', 'Windows']} />
                                <RowIcons names={['Raspberry Pi', 'Arduino']} />
                            </li>
                            <li>
                                <h4>GRAPHIC TOOLS</h4>
                                <RowIcons names={['Illustrator', 'Photoshop']} />
                            </li>
                        </ul>
                    </section>
                </div>
                <div>
                    <section className={style.section}>
                        <h2>EXPERIENCE</h2>
                        <hr/>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/gomore.jpg'
                                alt='GoMore logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>GoMore <span>Jan 2024 - Present</span></h3>
                                    <h5>Backend Engineer</h5>
                                    <h6>PHP (Laravel)</h6>
                                    <ul className={style.pointList}>
                                        <li>Maintaining a backend system</li>
                                    </ul>
                                </li>
                            </ul>
                        </article>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/digiiots.png'
                                alt='Digiiots logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>Digiiots <span>Jan 2024 - Mar 2024</span></h3>
                                    <h5>Software Backend Engineer</h5>
                                    <h6>TypeScript (Nest, Jest)/ MongoDB</h6>
                                    <ul className={style.pointList}>
                                        <li>Developed a MVP for file conversion service</li>
                                        <li>Debugged and fixed IoT service exceptions</li>
                                        <li>Refactored a microservice and created the test scripts, which have 100% code coverage</li>
                                    </ul>
                                </li>
                            </ul>
                        </article>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/biggo.jpg'
                                alt='BigGo logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>BigGo <span>Sep 2022 - Dec 2023</span></h3>
                                    <h5>Software Engineer</h5>
                                    <h6>TypeScript (Next, React)/ Python (FastAPI)/ SQLite/ HTML/ SASS/ K8S</h6>
                                    <ul className={style.pointList}>
                                        <li>Built 100% automated service for inspecting and analyzing data to fix incorrect ones and track system status</li>
                                        <li>Integrated 90% of web tools into a chrome extension to simplify and automate 70% daily-repetitive processes</li>
                                        <li>Developed and maintained a full-stack system, whose website was available for managing auto-collected data, of web crawlers</li>
                                    </ul>
                                </li>
                            </ul>
                        </article>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/circumcode.png'
                                alt='Circumcode logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>Circumcode (Laboratory) <span>May 2021 - Dec 2022</span></h3>
                                    <h5>Frontend Engineer/ Project Manager</h5>
                                    <h6>TypeScript (React)/ HTML/ SASS</h6>
                                    <ul className={style.pointList}>
                                        <li>Participated in software design and development</li>
                                        <li>Provided technical guidance and coordination of communication for teammates</li>
                                        <li>Executed project planning and monitored progress on it</li>
                                    </ul>
                                </li>
                            </ul>
                        </article>
                    </section>
                    <section className={style.section}>
                        <h2>EDUCATION</h2>
                        <hr/>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/nknu.png'
                                alt='NKNU logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>National Kaohsiung Normal University</h3>
                                    <h5>B.E, Software Engineering And Management</h5>
                                    <h6>Sep 2019 - Jun 2023</h6>
                                    <span>During the course I learned various knowledge.</span>
                                    <ul className={style.pointList}>
                                        <li>Programming languages (Java, Python, C#)</li>
                                        <li>Theory of computation (Data structure, Algorithm)</li>
                                        <li>Software engineering (OOP, MVC, UML)</li>
                                        <li>Math (Discrete mathematics, Linear algebra, Calculus)</li>
                                    </ul>
                                    <span>My capstone project is web software that simplify the web development process.</span>
                                </li>
                            </ul>
                        </article>
                        <article>
                            <StaticImage
                                className={style.image}
                                src='../../assets/ntsh.png'
                                alt='NTSH logo'
                                loading='lazy'
                            />
                            <ul className={style.bypassImage}>
                                <li>
                                    <h3>New Taipei Senior High School</h3>
                                    <h5>General Subjects</h5>
                                    <h6>Sep 2015 - Jul 2019</h6>
                                    <ul className={style.pointList}>
                                        <li>Begin learning programming</li>
                                        <li>Change the attitude and take the initiative to study</li>
                                        <li>My dream starts to grow from here</li>
                                    </ul>
                                </li>
                            </ul>
                        </article>
                    </section>
                </div>
            </div>
        </Layout>
    )
}
export default ResumePage;

export const Head: FC<PageProps> = ({ location }) => (
    <SEO
        title='Resume'
        pathname={location.pathname}
    />
)
