import React, { CSSProperties, ReactNode, useRef, useState } from 'react';

const intSpeedMillisecondPerFrame: number = 3;
const intFrameNumberTotal: number = 50;
const intMaxValue: number = 1;
const intMinValue: number = 0;
const fadeIn = (intOpacity: number, funcUpdate: (value: number)=>void, funcTriggerFinally?: ()=>void): NodeJS.Timer => {
    return setTimer(intOpacity, 1, funcUpdate, funcTriggerFinally);
}
const fadeOut = (intOpacity: number, funcUpdate: (value: number)=>void, funcTriggerFinally?: ()=>void): NodeJS.Timer => {
    return setTimer(intOpacity, -1, funcUpdate, funcTriggerFinally);
}
const setTimer = (intValue: number, intNumberSign: number, funcUpdate: (value: number)=>void, funcTriggerFinally = () => {}): NodeJS.Timer => {
    let timeoutID = setInterval(() => {
        intValue += (1 / intFrameNumberTotal) * intNumberSign;
        funcUpdate(intValue);

        if ((intValue < intMinValue) || (intValue > intMaxValue)) {
            clearInterval(timeoutID);
            funcTriggerFinally();
        }
    }, intSpeedMillisecondPerFrame);
    return timeoutID;
}

export const FadeIn = (props: {children: ReactNode}) => {
    const [floatElementOpacity, setElementOpacity] = useState(0);
    const isFirstRender = useRef(true);
    if (isFirstRender.current) {
        fadeIn(floatElementOpacity, (floatOpacity: number) => { setElementOpacity(floatOpacity) } );
    }

    isFirstRender.current = false;
    const Css: CSSProperties = {
        margin: '0',
        opacity: floatElementOpacity
    }
    return (
        <div style={Css}>
            {props.children}
        </div>
    )
}

export const FadeAnimation = (props: {children: ReactNode, isShow: boolean}) => {
    const [floatElementOpacity, setElementOpacity] = useState(0);
    const isAnimating = useRef<NodeJS.Timer>();
    const checkAnimationState = () => {
        if (isAnimating.current !== null) clearInterval(isAnimating.current);
    }

    const isShowPrevious = useRef(false);
    if (props.isShow && !isShowPrevious.current) {
        checkAnimationState();
        isAnimating.current = fadeIn(floatElementOpacity, (floatOpacity: number) => setElementOpacity(floatOpacity) );
    }
    else if (!props.isShow && isShowPrevious.current) {
        checkAnimationState();
        isAnimating.current = fadeOut(floatElementOpacity, (floatOpacity: number) => setElementOpacity(floatOpacity) );
    }
    isShowPrevious.current = props.isShow;

    const Css: CSSProperties = {
        margin: '0',
        display: 'inline',
        opacity: floatElementOpacity
    }
    return (
        <div style={Css}>
            {props.children}
        </div>
    )
}
