import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import type { Plugins } from '../../types/plugins';

type Props = {
    name: string,
    isFocus?: boolean,
    onClick?: () => void
}
const Icon: FC<Props> = ({ name, isFocus=false, onClick=() => {} }) => {
    const { allFile }: Queries.IconQuery = useStaticQuery(graphql`
        query Icon {
            allFile(filter: {relativeDirectory: {eq: "icon"}}) {
                nodes {
                    name
                    childImageSharp {
                        gatsbyImageData
                    }
                }
            }
        }
    `);
    const image = (
        allFile.nodes
            .find((file: {name: string}) => (file.name === name.toLowerCase()))
    ) as Plugins.GatsbyImageData;

    return (
        <picture
            className={`${style.picture} ${isFocus ? style.focused : ''}`}
            title={name}

            onClick={onClick}
        >
            <GatsbyImage
                className={style.image}
                image={getImage(image)!}
                alt={`${name.toLowerCase()} icon`}
                loading='lazy'
            />
        </picture>
    )
}
export default Icon;
