import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React, { useState } from 'react';
import * as style from './index.module.scss';
import { FadeAnimation } from '../FadeAnimation';

const Header = () => {
    const [isHover, setHoverState] = useState(false);
    const mainPath = (typeof window !== 'undefined')? window.location.pathname.match(/^(\/[a-zA-Z0-9]*)/)![1] : '';

    return (
        <header
            className={style.header}
        >
            <Link
                to='/'
                id={style.logo}
                onMouseEnter={() => setHoverState(true)}
                onMouseLeave={() => setHoverState(false)}
            >
                <StaticImage
                    className={style.img}
                    src='../../assets/SNinjo.png'
                    alt='SNinjo logo'
                    loading='eager'
                />
                <FadeAnimation isShow={isHover}>
                    <span>SNinjo.com</span>
                </FadeAnimation>
            </Link>

            <nav>
                <Link
                    to='/'
                    id={style.home}
                >
                    <FadeAnimation isShow={mainPath === '/'}>
                        <span className={style.pivot}>/</span>
                    </FadeAnimation>
                    <span>home</span>
                </Link>
                <Link
                    to='/projects'
                    id={style.about}
                >
                    <FadeAnimation isShow={mainPath === '/projects'}>
                        <span className={style.pivot}>/</span>
                    </FadeAnimation>
                    <span>projects</span>
                </Link>
                <Link
                    to='/resume'
                    id={style.resume}
                >
                    <FadeAnimation isShow={mainPath === '/resume'}>
                        <span className={style.pivot}>/</span>
                    </FadeAnimation>
                    <span>resume</span>
                </Link>
                <Link
                    to='/contact'
                    id={style.contact}
                >
                    <FadeAnimation isShow={mainPath === '/contact'}>
                        <span className={style.pivot}>/</span>
                    </FadeAnimation>
                    <span>contact</span>
                </Link>
            </nav>
        </header>
    )
}
export default Header;
